.search-bar {
  background-color: rgb(235, 235, 235);
  padding: 15px;
  border-radius: 15px;
  border: 1px rgb(201, 201, 201) solid;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  margin: 10px;
  text-align: center;
}

.search-bar__label {
  font-size: 25px;
  display: block;
  margin-bottom: 10px;
}

.search-bar__button {
  width: 70%;
  background: rgb(46, 46, 46);
  color: azure;
  border: rgb(46, 46, 46) 3px solid;
  border-radius: 5px;
  height: 30px;
  margin: 10px 0 10px 0;
}

.search-bar__button:focus {
  outline: none;
}

.search-bar__button:hover {
  background: white;
  color: rgb(46, 46, 46);
  font-weight: bold;
}

.search-bar input,
.search-bar label,
.search-bar select {
  display: inline-block;
  font-size: 18px;
  font-weight: bold;
  padding: 4px;
}

.search-bar input {
  text-align: center;
}

.search-bar input[type="radio"] {
  -webkit-appearance: none;
  border: black 1px solid;
  width: 1rem;
  height: 1rem;
}

.search-bar input[type="radio"]:checked {
  background: rgb(39, 39, 39);
  border: 2px solid rgb(39, 39, 39);
  border-radius: 50%;
}

.search-bar input[type="radio"]:focus {
  outline: none;
}

.search-bar__options {
  text-align: right;
}
